  color: rgb(54, 69, 79);
} */

/*  MEDIA QUERIES MEDIUM SCREEN */

@media screen and (max-width: 1024px) {
  .portfolio-container {
    grid-template-columns: 1fr 1fr;
    gap: 1.2rem;
  }
}

/*  MEDIA QUERIES SMALL SCREEN */

@media screen and (max-width: 600px) {
  .portfolio-container {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
}
