header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header_container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* RESUME */
.resume {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* SOCIALS */
.header-socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header-socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/* ME IMAGE */
.me {
  background: linear-gradient(var(--color-light), transparent);
  width: 20rem;

  height: 20rem;
  position: absolute;
  left: calc(50% - 10rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  /* padding: 5rem 1.5rem 1.5rem 1.5rem; */
}

/* SCROLLING */
.scroll-down {
  position: absolute;
  right: 02.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9;
}

/*  MEDIA QUERIES MEDIUM SCREEN */

@media screen and (max-width: 1024px) {
  .header {
    height: 68vh;
  }
}

/*  MEDIA QUERIES SMALL SCREEN */

@media screen and (max-width: 600px) {
  .header {
    height: 100vh;
  }
  .header-socials,
  .scroll-down {
    display: none;
  }
  
}
